












































import { Component, Vue } from "vue-property-decorator";
import { State, Action } from 'vuex-class';

@Component({})
export default class Footer extends Vue {
    @State("pc")
    pc!: number;

    @Action("savePathName")
    savePathName!:Function;

    push (value, name) {
        this.savePathName(name);
        this.$router.push(value);
    }
}
